@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;1,400;1,800&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* theming */
:root {
  /* colors */
  --white: #fff;
  --grey: #ccc;
  --black: #000;

  /* theme colors */
  --navbar-background-color: transparent;
  --navbar-scroll-background-color: var(--black);
  --logo-color: var(--white);
  --logo-outline-color: var(--black);
  --footer-background: var(--black);
  --footer-text: var(--white);
  --nav-bar-links-button-color: var(--grey);
}

@use './src/assets/colors'

.minecraft-coordinate{display:flex;margin-bottom:96px;justify-content:space-between;background-color:#fff;border:1px solid #ccc}.minecraft-coordinate--image-left{flex-direction:row-reverse}@media only screen and (max-width: 600px){.minecraft-coordinate{flex-direction:column;margin-bottom:48px}}.minecraft-coordinate__text{width:100%;padding:12px}.minecraft-coordinate__image{display:flex;width:600px;height:320px;background-size:cover;background-repeat:no-repeat;background-position:50% 0px;flex-shrink:0}@media only screen and (max-width: 1000px){.minecraft-coordinate__image{width:480px;height:280px}}@media only screen and (max-width: 600px){.minecraft-coordinate__image{width:100%;margin-top:24px}}

.youtube-video{position:relative;height:100%;width:100%}.youtube-video__iframe{position:absolute;top:0;left:0;width:100%;height:100%}

.media-with-description{display:flex;margin-bottom:96px;justify-content:space-between;background-color:#fff;border:1px solid #ccc}.media-with-description--media-left{flex-direction:row-reverse}@media only screen and (max-width: 600px){.media-with-description{flex-direction:column;margin-bottom:48px}}.media-with-description__description{width:50%;flex-grow:0;padding:12px}@media only screen and (max-width: 600px){.media-with-description__description{width:100%}}.media-with-description__media{display:flex;width:600px;height:320px}@media only screen and (max-width: 1000px){.media-with-description__media{width:480px;height:280px}}@media only screen and (max-width: 600px){.media-with-description__media{width:100%;margin-top:24px}}.media-with-description__image{height:100%;width:100%;background-size:cover;background-repeat:no-repeat;background-position:50% 0px}

.main-body{display:flex;justify-content:center;align-items:center}.main-body__content{width:100%;max-width:800px;min-height:800px;padding:0 16px}

@-webkit-keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.loader{display:flex;position:absolute;left:0;top:0;height:100vh;width:100vw;justify-content:center;align-items:center}.loader__icon{height:32px;width:32px;font-size:32px;-webkit-animation:5s spin infinite linear;animation:5s spin infinite linear}

.page{background-color:var(--primary-color)}.page__description{margin-bottom:96px}@media only screen and (max-width: 600px){.page__description{margin-bottom:48px}}

